<template>
    <div class="page">
        <div class="page-wrapper">
            <stepbar
            :steps="steps"
            ></stepbar>

            <step0
            v-if="steps.now == 0"
            ></step0>
            <step1
            v-if="steps.now == 1"
            ></step1>
            <step2
            v-if="steps.now == 2"
            ></step2>
            <step3
            v-if="steps.now == 3"
            ></step3>
            <step4
            v-if="steps.now == 4"
            ></step4>
        </div>
    </div>
</template>

<script>
import stepbar from '@/pages/signUp/_stepBar.vue';

import step0 from '@/pages/signUp/_step0.vue';
import step1 from '@/pages/signUp/_step1.vue';
import step2 from '@/pages/signUp/_step2.vue';
import step3 from '@/pages/signUp/_step3.vue';
import step4 from '@/pages/signUp/_step4.vue';

export default {
    name: 'signup',
    data(){
        return{
            steps:{
                now: 0
            }
        }
    },
    components:{
        stepbar,
        step0,
        step1,
        step2,
        step3,
        step4
    }

}
</script>

<style lang="scss">
    @import '@/assets/pages/signUp/signUp.scss';
</style>




