<template>
    <div class="page-content step0">
        <h1 class="title">
            Create Account
        </h1>
        <h3 class="sub-title">
            Glad to see you here!
        </h3>
        <el-form status-icon :model="accountForm" :rules="rules" ref="accountForm" label-width="100px" class="account-form">
            <el-form-item label="Account" prop='account' required>
                <el-input type="email" v-model="accountForm.account" auto-complete="off" placeholder="example@email.com"></el-input>
            </el-form-item>
            <el-form-item label="Password" prop="pwd" required>
                <el-input type="password" v-model="accountForm.pwd" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Confirm Password" prop="checkPwd" required>
                <el-input type="password" v-model="accountForm.checkPwd" auto-complete="off"></el-input>
            </el-form-item>
            <el-button class="btn" type="primary" @click="submitForm('accountForm')" :disabled="!buttonStatus">SUBMIT and NEXT</el-button>
        </el-form>
    </div>
</template>

<script>
export default {
    name: 'step0',
    data(){
        var validatePwd = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('REQUIRED'));
                this.accountFormStatus = false;
            } else{
                if(value.length < 8){
                    this.accountFormStatus = false;
                    callback(new Error('MINIMUM 8 CHARACTERS'));
                }
                 if (this.accountForm.checkPwd !== '') {
                    this.$refs.accountForm.validateField('checkPwd');
                    this.accountFormStatus = false;
                }
                this.accountFormStatus = true;
                callback();
            }
        };
        var validatePwd2 = (rule, value ,callback) => {
             if (value === '') {
                callback(new Error('REQUIRED'));
                this.accountFormStatus = false;
            } else if (value !== this.accountForm.pwd) {
                callback(new Error('Two inputs don\'t match!'));
                this.accountFormStatus = false;
            } else {
                this.accountFormStatus = true;
                callback();
            }
        };
        return{           
            accountForm:{
                account: '',
                pwd: '',
                checkPwd: '',
            },
            rules:{
                account:[
                    {
                        required: true, message:'REQUIRED', trigger: 'blur'
                    },
                    {
                        type: 'email', message:'INVALID EMAIL', trigger: 'blur'
                    }
                ],
                pwd:[
                    {
                        validator: validatePwd, trigger: 'blur'
                    }
                ],
                checkPwd:[
                    {
                        validator: validatePwd2, trigger: 'blur'
                    }
                ]
            },
            accountFormStatus: true,
        }
    },
    computed:{
        buttonStatus(){
            return this.accountFormStatus;         
        }
        
    },
    methods:{
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    this.accountFormStatus = true;
                } else {
                    // console.log('error submit!!');
                    this.accountFormStatus = false;
                    return false;
                }
            });
      },
    }

}
</script>




