<template>
    <div class="steps-bar">
        <div :class="['step',{'active':index == steps.now, 'complete': index < steps.now}]" :key="index" v-for="(step,index) in 4">
            <i 
            :class="[
            {
            'far fa-circle': index > steps.now,
            'far fa-dot-circle': index == steps.now && steps.now != 4,
            'fas fa-check-circle': index < steps.now || steps.now == 4
            }]"
            ></i>
        </div>
    </div>
</template>

<script>
export default {
    name: 'stepbar',
    props:['steps'],
    data(){
        return{
            
        }
    },
}
</script>

<style lang="scss">
@import '@/assets/_mixins.scss';
.steps-bar{
    margin: 30px auto;
    margin-bottom: 0;
    max-width: 300px;
    display: flex;
    flex-wrap: wrap;
    @include rwd_down(767){
        margin: 20px auto;
        margin-bottom: 0;
    }
    .step{
        width: 16px;
        position: relative;
        text-align: left;
        &:not(:last-child){
            width: calc((100% - 16px)/3) ;
            &:after{
                content: '';
                position: absolute;
                top: calc(50% - 2px);
                width: calc(100% - 16px);
                height: 2px;
                background-color: #BEE1FF;
            }
        }
        i{
            font-size: 16px;
            color: #BEE1FF;
        }
        &.active{
            i{
                color: #0275D8;
            }
        }
        &.complete{
            i{
                color: #0275D8;
            }
            &:after{
                background-color: #0275D8;
            }

            
        }
        
    }
}
</style>




